@import url("https://fonts.googleapis.com/css2?family=Bigelow+Rules&display=swap");
.App {
  min-height: 100vh;
  background-image: linear-gradient(
    45deg,
    #ff9a9e 0%,
    #fad0c4 99%,
    #fad0c4 100%
  );
}

.search-form {
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.search-bar {
  width: 50%;
  border: none;
  padding: 10px;
  font-size: 16px;
  outline: none;
}
.search-button {
  background: lightcoral;
  border: none;
  padding: 10px 20px;
  outline: none;
  color: white;
  cursor: pointer;
}
.recipes {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
.title {
  text-shadow: 0px 5px 15px #2b2b2c;
  margin: 0;
  text-align: center;
  font-size: 72px;
  font-family: "Bigelow Rules", cursive;
  letter-spacing: 2px;
  font-weight: bold;
  color: rgb(230, 30, 30);
}
